* {
  margin: 0
}

body {
  background-color: #1e1e1e;
  margin: 0;
  font-family: 'Raleway';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 /* mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));  fading gradient */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.breakpoint__medium-up .home-intro{font-size:65px;padding:10px 0;font-weight:bold}.breakpoint__medium-up .home-button{font-family:'Raleway';font-weight:bold;font-size:30px;text-transform:lowercase;background-color:#1e1e1e;border:1px solid #f1b10e;color:#f1b10e;margin-top:20px}.breakpoint__medium-up .home-button:hover{background-color:#f1b10e;color:#1e1e1e}.breakpoint__small-down .home-intro{font-size:50px;font-weight:bold;padding:5px 0}.breakpoint__small-down .home-button{font-family:'Raleway';font-weight:bold;font-size:25px;text-transform:lowercase;background-color:#1e1e1e;border:1px solid #f1b10e;color:#f1b10e;margin-top:20px}.breakpoint__small-down .home-button:hover{background-color:#f1b10e;color:#1e1e1e}.home{display:flex;height:100vh;align-items:center;color:#f1b10e;padding:0 50px}.home-left{display:flex;flex-direction:column}

.header {
  display: flex;
  align-items: center;
  height: 60px;
  border-bottom: 1px solid gray;
  font-family: 'Poppins', sans-serif;
}

.header-left {
  flex: 1 1;
}

.header-right {
  display: flex;
  justify-content: space-evenly;
}

.header-nav {
  text-decoration: none;
}

.header-options {
  padding: 0 20px;
  color: black;
  font-size: 20px;
}

.header-options:hover {
  transition: 0.5s;
  color: rgb(241, 177, 14);
}
.breakpoint__large-up .about{display:flex;padding:40px 50px}.breakpoint__large-up .about-right{flex:0.5 1}.breakpoint__large-up .about-left{flex:0.5 1}.breakpoint__large-up .about-title{padding-bottom:40px}.breakpoint__large-up .about-details{padding-top:85px}.breakpoint__medium-down .about{display:flex;flex-direction:column;align-items:center;padding:20px 50px}.breakpoint__medium-down .about-title{padding:40px 0}.breakpoint__medium-down .about-details{padding:40px 0}.breakpoint__medium-down .about-image{padding:20px 0}.about-image{width:500px;height:350px}.about-title{color:#f1b10e;font-size:40px;font-weight:bold}.about-details{font-size:18px;color:white}

.breakpoint__large-up .project-image{width:650px;height:350px;object-fit:fill;border:1px solid black}.breakpoint__large-up .project-container{padding:20px}.breakpoint__medium-down .project-image{height:225px;width:400px;object-fit:fill;border:1px solid black}.breakpoint__medium-down .project-container{padding:20px}.project-container:hover{filter:grayscale(100%);-webkit-filter:grayscale(100%);-webkit-transition:all 1s ease;cursor:pointer}.project-container{filter:grayscale(0%);filter:gray;-webkit-filter:grayscale(0%);filter:none;transition:1s ease}

.work-project{display:flex;flex-wrap:wrap;justify-content:center}.work{padding:40px 0}.breakpoint__large-up .work-title{color:#f1b10e;font-size:40px;font-weight:bold;padding:40px 50px}.breakpoint__medium-down .work-title{color:#f1b10e;font-size:40px;font-weight:bold;padding:40px 50px}

.breakpoint__large-up .contact{display:flex}.breakpoint__large-up .contact-left{flex:0.5 1;padding:50px}.breakpoint__large-up .contact-right{flex:0.6 1}.breakpoint__large-up .contact-form{display:flex;flex-direction:column;padding:10px 0}.breakpoint__large-up .contact-title{padding:10px 0}.breakpoint__large-up .contact-info{padding:20px 0}.breakpoint__medium-down .contact{display:flex;flex-direction:column;padding:50px}.breakpoint__medium-down .contact-info{padding:20px 0}.breakpoint__medium-down .contact-title{color:#f1b10e;padding:20px 0}.breakpoint__medium-down .contact-form{display:flex;flex-direction:column}.breakpoint__medium-down .contact-row{display:flex}.breakpoint__medium-down #name,.breakpoint__medium-down #email{flex:0.5 1}.breakpoint__medium-down #name{margin-right:20px}.breakpoint__medium-down .contact-form{padding-top:20px;padding-bottom:40px}.contact-title{font-size:40px;font-weight:bold;color:#f1b10e}.contact-info{color:white;font-size:18px}#name,#phone,#email,#message{background-color:#1e1e1e;margin-bottom:20px;padding:15px;border:1px solid #f1b10e;font-family:'Raleway';color:white;font-size:18px}#name,#phone,#email,#message,#submit-button:focus{outline:none}#submit-button{padding:10px;border:1px solid #f1b10e;background-color:#1E1E1E;font-family:'Raleway';color:darkgray;font-size:18px}#submit-button:hover{background-color:#f1b10e;transition:0.5s;opacity:0.8;color:#1e1e1e;cursor:pointer}.contact-map-box{display:flex;flex-direction:column;font-size:18px}.contact-soc{text-decoration:none;padding-left:8px}.contact-box-info{display:flex;align-items:center}


